body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box; }

.App {
  margin: 0 0 1.5rem 0; }
  .App .title {
    margin-bottom: 3rem; }

.card {
  background-color: #e3e3e3;
  width: 100%;
  border-radius: 0.5rem;
  padding: 2rem; }
  .card-title {
    margin: 0; }
  .card-subtitle {
    color: #666; }
  .card-description {
    text-align: center;
    font-size: 3.5rem;
    margin: 1rem 0 2rem; }

.cards-group {
  margin: 0;
  padding: 0; }
  .cards-group-item {
    margin-bottom: 1rem; }
  .cards-group .slick-slide > div {
    margin: 0 1rem; }
  .cards-group .slick-dots {
    bottom: 1rem; }
    .cards-group .slick-dots li button:before {
      font-size: 10px; }

.header {
  position: -webkit-sticky;
  position: sticky;
  z-index: 1;
  background: white;
  width: 100%;
  padding: 1rem;
  top: 0;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.2);
  font-weight: bold;
  text-transform: uppercase;
  font-size: smaller;
  margin-bottom: 1rem; }

